import React from 'react';
import s from './FriendsElement.module.css';
import {NavLink} from "react-router-dom";

const FriendsElement = ({followed, ...props}) => {
    return (
        followed
            ? <div className={s.friendsItem}>
                <div className={s.img}><img src="http://www.sclance.com/pngs/avatar-icon-png/avatar_icon_png_71054.png"
                                            alt="avatar"/>
                </div>
                <div className={s.textBlock}>
                    <div className={s.name} id={props.id}>{props.name}</div>
                    <NavLink to="/Dialogs">Write message...</NavLink>
                </div>
              </div>
            : null

)
    ;
};

export default FriendsElement;