import React from 'react';
import s from "./ProfileInfo.module.css";
import Preloader from "../../common/Preloader/Preloader";
import userAvatar from "../../../Assets/images/avatar.png";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = (props) => {

    if (!props.profile) {
        return <Preloader/>
    }

    return (
        <div>
            <div className={s.info}>
                <div className={s.infoImage}>
                    <img src={props.profile.photos.large != null ? props.profile.photos.large : userAvatar} alt=""/>
                </div>
                <div className={s.profileInfo}>
                    <h3 className={s.profileHeader}>{props.profile.fullName}</h3>
                    <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
                    {props.profile.lookingForAJob
                        ? <div className={s.workT}>Looking for a job</div>
                        : <div className={s.workF}>Already working</div>
                    }
                    <div className={s.wrapper_description}>
                        <h4 className={s.description}>{props.profile.lookingForAJobDescription || 'No description'}</h4>
                    </div>

                    <div className={s.links}>
                        <p>Links to :</p>
                        <a href={props.profile.contacts.facebook} className={s.items}>
                            <i className="fa fa-facebook-square"></i>
                        </a>
                        <a href={props.profile.contacts.vk} className={s.items}>
                            <i className="fa fa-vk"></i>
                        </a>
                        <a href={props.profile.contacts.twitter} className={`${s.items} ${s.twit}`}>
                            <i className="fa fa-twitter-square"></i>
                        </a>
                        <a href={props.profile.contacts.instagram} className={`${s.items} ${s.inst}`}>
                            <i className="fa fa-instagram"></i>
                        </a>
                        <a href={props.profile.contacts.github} className={`${s.items} ${s.git}`}>
                            <i className="fa fa-github-square"></i>
                        </a>
                    </div>

                </div>
            </div>
        </div>
    );
};

export default ProfileInfo;

